
import XHeader from 'vux/src/components/x-header/index.vue'

import { mapGetters } from "vuex";
export default {
components: {
XHeader
},
computed: {
...mapGetters(["personDemo"])
},
created() {
this.$store.dispatch("getPersonDemo", {
teacher_id: this.$route.params.personid
});
},
activated() {
this.$store.dispatch("getPersonDemo", {
teacher_id: this.$route.params.personid
});
},
deactivated() {
this.demo = [];
},
watch: {
personDemo: function(val) {
this.demo = val;
}
},
data() {
return {
demo: []
};
},
methods: {}
};



